

/**
 * 
 * eg2:4  -->  02:04
 * @param value -  h:m:s  
 */
const formatTime = (value: string)=> {
    let time = "";
    const s = value.split(':');
    let i = 0;
    for (; i < s.length - 1; i++) {
        time += s[i].length == 1 ? ("0" + s[i]) : s[i];
        time += ":";
    }
    time += s[i].length == 1 ? ("0" + s[i]) : s[i];

    return time;
}

/**
 * 
 * @param  value - 
 */
export const transTime = (value: number)=> {
    let time = "";
    const h = parseInt(String(value / 3600));
    value %= 3600;
    const m = parseInt(String(value / 60));
    const s = parseInt(String(value % 60));
    if (h > 0) {
        time = formatTime(h + ":" + m + ":" + s);
    } else {
        time = formatTime(m + ":" + s);
    }
    return time;
}

/*
 * @description 
 * @param 
 * @return 
 */
export const getComputedStyle = (obj: any, style: string)=> {
    if(obj.currentStyle){
        return obj.currentStyle[style].replace('px', '') //ie
    }else {
        return  (document as any).defaultView.getComputedStyle(obj,null)[style].replace('px', '')//
    }
}