import customAudio from '@/components/custom-audio/index.vue'; //

const main = {
  install(Vue: any, options: any) {
    console.log(customAudio.name+'');
    Vue.component(customAudio.name, customAudio); //
  }
};

// 
if (typeof window !== 'undefined' && window.Vue) {
  window.Vue.use(main);
}

export default main;
